<template>
    <fieldset :id="id" class="games">
        <div
            v-for="(option, index) in options"
            :key="option.text"
            class="games__container"
        >
            <input
                :id="id + index"
                class="games__radio"
                :name="id"
                type="radio"
                :value="option.value"
                :checked="option.value === value"
                :disabled="disabled"
                :required="required"
                @change="updateValue(option.value)"
            />

            <label :for="id + index">
                <div
                    :class="[
                        'games__button',
                        { games__button_active: option.value === value },
                    ]"
                    @click.stop
                >
                    <img
                        :class="[
                            'games__icon',
                            { games__icon_active: option.value === value },
                        ]"
                        :src="getGameIconURL(option.value)"
                        alt="Game icon"
                    />
                </div>
            </label>
        </div>
    </fieldset>
</template>

<script>
/* eslint-disable global-require */
/* eslint-disable import/no-dynamic-require */
export default {
    model: {
        event: 'change',
    },
    props: {
        id: {
            type: String,
            required: true,
        },
        value: {
            type: [String, Number, Boolean, Object],
            default: null,
        },
        options: {
            type: [Array],
            required: true,
        },
        required: {
            type: Boolean,
            default: false,
        },
        disabled: {
            type: Boolean,
            default: false,
        },
    },
    methods: {
        updateValue(value) {
            this.$emit('change', value);
        },

        getGameIconURL(iconName) {
            return require(`@/assets/images/icons/${iconName}.svg`);
        },
    },
};
</script>

<style scoped>
.games {
    margin: 0;
    padding: 0;
    border: none;
    background-color: #111111;
    padding: 9px;
    border-radius: 32px;
    width: 100%;
    max-width: 64px;
}

.games__container {
    position: relative;
    margin-bottom: 9px;
}

.games__container:last-of-type {
    margin-bottom: 0;
}

.games__radio {
    position: absolute;
    top: 0;
    left: 0;
    width: 46px;
    height: 46px;
    opacity: 0;
}

.games__button {
    width: 46px;
    height: 46px;
    margin: 0;
    padding: 0;
    border: none;
    border-radius: 50%;
    background-color: #212121;
    cursor: pointer;
    outline: none;
    display: flex;
    justify-content: center;
    align-items: center;

    transition: all 0.2s ease-in-out;
}

.games__button:hover {
    opacity: 0.8;
}

.games__button_active {
    background-color: #0091ff;
}

.games__icon {
    transition: all 0.2s ease-in-out;

    filter: invert(47%) sepia(1%) saturate(0%) hue-rotate(185deg)
        brightness(97%) contrast(91%);
}

.games__icon_active {
    filter: invert(100%) sepia(0%) saturate(7500%) hue-rotate(108deg)
        brightness(103%) contrast(107%);
}

.games__button:hover .games__icon_active {
    filter: invert(100%) sepia(0%) saturate(7500%) hue-rotate(108deg)
        brightness(103%) contrast(107%);
}
</style>
