<template>
    <div class="modal">
        <div class="modal__overlay" @click="closeModal" />

        <div v-if="isShown" class="modal__body">
            <div class="modal__container">
                <component :is="content" class="modal__content"></component>
            </div>
        </div>
    </div>
</template>

<script>
import TheModalFeedback from '@/components/modal/TheModalFeedback.vue';
import TheModalResetSettings from '@/components/modal/TheModalResetSettings.vue';
import TheModelRestartGame from '@/components/modal/TheModelRestartGame.vue';
import TheModalResult from '@/components/modal/TheModalResult.vue';

export default {
    components: {
        TheModalFeedback,
        TheModalResetSettings,
        TheModelRestartGame,
        TheModalResult,
    },

    data() {
        return {
            content: null,
            isShown: false,
        };
    },

    computed: {},

    created() {
        this.content = this.$store.getters['modal/getModalContent'];
        this.isShown = this.$store.getters['modal/getModalShown'];
    },

    mounted() {
        window.addEventListener('keyup', this.closeModalOnKeyupEsc);
    },

    destroyed() {
        window.removeEventListener('keyup', this.closeModalOnKeyupEsc);
    },

    methods: {
        closeModalOnKeyupEsc(event) {
            if (event.key === 'Escape') this.closeModal();
        },

        closeModal() {
            this.isShown = this.$store.dispatch('modal/closeModal');
        },
    },
};
</script>

