    animation: 0.2s ease 0s 1 fade;
}

.modal__body {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -55%);
    animation: 0.2s ease 0s 1 fade, 0.2s ease 0s 1 moveFromTop;
    display: flex;

    background-color: #191919;
    border-radius: 24px;
    border: solid 1px #202020;
    background-color: #191919;
}

.modal__logo {
    width: 34px;
    height: 34px;
}

.modal__container {
    display: flex;
    flex-direction: column;
    position: relative;
    width: 100%;
}

.modal__close {
    position: absolute;
    top: 5px;
    right: 5px;
    width: 16px;
